import React, { useState, useEffect } from "react";
import axios from "axios";
import { useNavigate, useLocation } from "react-router-dom";
import { usereditget } from "../../api/user";
import { updateuser } from "../../api/user";
import { adduser } from "../../api/user";
import { EyeFill, EyeSlashFill } from 'react-bootstrap-icons';
// import './Style.css'
const UserForm = () => {
  const navigate = useNavigate();
  const location = useLocation();
  const [name, setName] = useState("");
  const [email, setEmail] = useState("");
  const [password, setPassword] = useState("");
  const [showPassword, setShowPassword] = useState(false);
  const [id , setID]= useState("")
  const actionType = location.state?.actionType;
  let userId = location.state?.userId;
//   userId =  id;
  console.log(userId)
  useEffect(() => {
    if (actionType === "edit" ) {
     
      console.log(userId)
    usereditget(userId)
          .then((response) => {
            const user = response;
            console.log(user)
            setName(user.name);
            setEmail(user.email);
            setPassword(user.password);
          })
          .catch((error) => {
            console.log(error);
          });
      
    }
  }, [actionType, location]);

  const handleSubmit = (event) => {
    event.preventDefault();

    const user = {
       id : userId, 
      name: name,
      email: email,
      password : password,
    };

    if (actionType === "edit") {
      const userId = location.state?.userId;
      console.log(userId)
    
          updateuser(userId , name, email,password)
          .then((response) => {
            console.log("User updated successfully:", response.data);
            navigate("../table");
          })
          .catch((error) => {
            console.log(error);
          });
      
    } else if (actionType === "add") {
      
       adduser( name, email,password)
        .then((response) => {
          console.log("User added successfully:", response.data);
          navigate("../table");
        })
        .catch((error) => {
          console.log(error);
        });
    }
  };

  const handleCancel = () => {
    navigate("../table");
  };
  const togglePasswordVisibility = () => {
    setShowPassword(!showPassword);
  };
  return (
    <div className="user-form-container caliber-font">
      <form onSubmit={handleSubmit} className="user-form ">
        <h2 className="form-title">
          {actionType === "edit" ? "Edit User" : "Add User"}
        </h2>
        <div className="form-group">
          <label htmlFor="name">Name:</label>
          <input
            type="text"
            id="name"
            className="form-input "
            value={name}
            onChange={(e) => setName(e.target.value)}
          />
        </div>
        <div className="form-group">
          <label htmlFor="email">Email:</label>
          <input
            type="email"
            id="email"
            className="form-input"
            value={email}
            onChange={(e) => setEmail(e.target.value)}
          />
        </div>
        <div className="form-group">
          <label htmlFor="password">Password:</label>
          <input
             type={showPassword ? "text" : "password"}
            id="password"
            className="form-input"
            value={password}
            onChange={(e) => setPassword(e.target.value)}
          />
           <span style={{float:"right", marginLeft:"-25px" , position: "absolute"}} className="password-toggle" onClick={togglePasswordVisibility}>
              {showPassword ?<EyeFill />  : <EyeSlashFill />}
            </span>
        </div>
        <div className="form-buttons">
          <button type="submit" className="submit-button">
            {actionType === "edit" ? "Save" : "Add"}
          </button>
          <button type="button" className="cancel-button" onClick={handleCancel}>
            Cancel
          </button>
        </div>
      </form>
    </div>
  );
};

export default UserForm;
